$font-family: "Trykker", 'Times New Roman', Times, serif;
$font-color:white;
$hover-color:rgb(56, 11, 11);
$background-color:#116466;

.my-story {
    margin: auto;
    padding-bottom: 40px;
    text-align: center;
    h1 {
        margin: auto auto 20px; 
        padding: 10px 0 15px;
        width: 100%;     
        font-weight: bold;
        font-size: 2.4rem;
        font-family: $font-family;
        background-color: $hover-color;
        color: $font-color;
    }
        
    p {
        margin: auto;
        width: 70%;
    }
    
    ul {
        margin: 20px auto;
        padding-top: 5px;
        width: 20%;
        text-align: center;
        border-radius: 10%;
        background-color: black;
        li {
            img {
                max-width: 100%;
                height: auto;
                border-radius: 50%;
            }
     
            h2, 
            h3 {
                margin: 0 10px;
                font-weight: bold;
                color: $font-color;
            }

            h2 {
                padding-top: 8px;
                font-size: 1.875rem;
            }
    
            h3 {
                padding-bottom: 10px;
                font-size: 1.463rem;
            }
            
            p {
                padding: 10px 0 15px;
                width: 100%;
                border-bottom-left-radius: 10px;
                border-bottom-right-radius: 10px;
                text-align: center;
                background-color: $background-color;
                a {
                    height: 0;
                    width: 0;
                    font-weight: bold;
                    font-size: 1.563rem;
                    color: $font-color;
                    &:hover {
                        color: $hover-color;
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 1500px) {
  
    .my-story {
        ul {
            width: 25%;
        }
    }
}
  
@media only screen and (max-width: 1200px) {
  
    .my-story { 
        h1 {
            font-size: 2rem;
        }
  
        p {
            width: 90%;
        }
  
        ul {
            width: 30%;
            li {
                h2 {
                    font-size: 1.7rem;
                }
    
                h3 {
                    font-size: 1.4rem;
                }
                
                p a {
                    font-size: 1.4rem;
                }
            }
        }
    }
} 

@media only screen and (max-width: 1000px) {
  
    .my-story {
        padding: 25px 0 20px;
        h1 {
            padding: 10px 0;
            font-size: 1.6rem;
        }
  
        ul {
            margin: 10px auto;
            width: 35%;
            li {
                h2 {
                    font-size: 1.55rem;
                }
        
                h3,
                p a {
                    font-size: 1.35rem;
                }
            }
        }
    }
}

@media only screen and (max-width: 775px) {
    
    .my-story {
        ul {
            width: 43%;
            li {
                h2 {
                    font-size: 1.45rem;
                }
    
                h3,
                p a {
                    font-size: 1.25rem;
                }
            }
        }
    }
} 

@media only screen and (max-width: 600px) {
    
    .my-story {
        ul {
          width: 55%;
        }
    }
}
  
@media only screen and (max-width: 500px) {
    
    .my-story {
        padding: 0 0 20px;
        h1 {
            margin: auto auto 10px;
            font-size: 1.25rem;
        }
  
        ul {
            width: 60%;
            li {
                h2 {
                    font-size: 1.25rem;
                }
    
                h3 {
                    font-size: 1.05rem;
                }
    
                p a {
                    font-size: 1.05rem;
                    &:hover {
                        color: $font-color;
                    }
    
                    &:active {
                        color: $hover-color;
                    }
                }
            }
        }
    }
} 

@media only screen and (max-width: 370px) {
    
    .my-story {
        ul {
            width: 70%;    
        }
    }
}
  